* {
  padding: 0;
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: all 450ms;
}

body {
  /* -webkit-font-smoothing: antialiased; */
  -moz-osx-font-smoothing: grayscale;
}

.absolute {
  position: absolute;
}

.full-size {
  width: 100%;
  height: 100%;
}

.social-media-item {
  width: 50px;
  height: 50px;
}

li {
  list-style: none;
}

.circle {
  border-radius: 50%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dark-background {
  background: black !important;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
  top: 0;
}

.light-background {
  background-color: white !important;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
}

.stick-top {
  top: 0 !important;
}

.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page {
  position: relative;
}

.page-name {
  z-index: 2000;
  position: relative;
  left: 50px;
  top: 80px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 100%;
  text-overflow: clip;
  font-style: italic;
  font-weight: 100;
  font-size: 15pt;
}

.page-name::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 100px;
  background: white;
  left: -20px;
  top: -3px;
}

.page-name-position {
  position: absolute;
  top: 0vh;
  left: 10px;
}

.hide {
  display: none !important;
}

.white-text {
  color: white;
}

@media (max-width: 768px) {
  .page-name-position {
    position: absolute;
    top: -8vh;
    left: 10px;
  }

  .collapse-on-mobile {
    display: none !important;
    width: 0 !important;
    height: 0 !important;
  }
}

